import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {layerEvents} from '@servicenow/library-enhanced-utils';
import focusBehavior from '@servicenow/behavior-focus';
import {t} from 'sn-translate';
import styles from './_now-modal.scss';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import {actionTypes} from '@servicenow/ui-core';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const {
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

const addModalToStack = (id, dispatch) => {
	layerEvents.add({
		id,
		events: ['keydown'],
		callback: (event) => {
			if (event.key !== 'Escape') return;

			dispatch(() => {
				return {
					type: 'NOW_MODAL#OPENED_SET',
					payload: {value: false}
				};
			});
		}
	});
};

const renderFooter = (footerActionsObj, dispatch) => {
	if (!footerActionsObj) {
		return;
	}
	return (
		<div className="now-modal-footer">
			{renderFooterActions(footerActionsObj, dispatch)}
		</div>
	);
};

const renderFooterActions = (footerActionsObj, dispatch) => {
	return footerActionsObj
		.map((footerAction) => {
			const {variant, label, bare, disabled, clickActionType} = footerAction;

			const actionProps = {
				className: 'now-modal-footer-button',
				variant,
				label,
				disabled,
				'on-click': () => {
					if (disabled) return;

					dispatch(() => {
						return {
							type: clickActionType || 'NOW_MODAL#FOOTER_ACTION_CLICKED',
							payload: {
								action: footerAction,
								/* `action.payload.footerAction` is deprecated, keeping it around
								 * to not break API, use `action` instead
								 */
								footerAction
							}
						};
					});
				}
			};

			return bare ? (
				<now-button-bare {...actionProps} />
			) : (
				<now-button {...actionProps} />
			);
		})
		.reverse();
};

const view = (state, {dispatch}) => {
	const {componentId, headerEl, headerLabelEl} = state;
	const {
		size,
		headerLabel,
		content,
		contentFullWidth,
		opened,
		footerActions,
		legacyMode
	} = state.properties;
	const footerActionsObj = footerActions ? [...footerActions] : undefined;

	const showTooltip =
		headerEl && headerLabelEl
			? headerLabelEl.scrollWidth > headerLabelEl.clientWidth
			: false;

	return (
		<div className="now-modal-overlay" hidden={!opened}>
			<div
				role="dialog"
				aria-labelledby={`modalHeading-${componentId}`}
				class={{
					'now-modal': true,
					['-' + size]: size,
					['is-opened']: opened,
					'is-legacy-mode': legacyMode
				}}>
				<div className="now-modal-dialog">
					<div className="now-modal-header">
						<h1
							id={`modalHeading-${componentId}`}
							className="now-modal-heading">
							<span className="now-line-height-crop">
								<span className="now-modal-heading-label">{headerLabel}</span>
							</span>
						</h1>
						{showTooltip ? (
							<now-tooltip target-ref={headerEl} content={headerLabel} />
						) : (
							undefined
						)}
						<now-button-iconic
							id="now-modal-close-button"
							icon="close-fill"
							variant="tertiary"
							size="md"
							bare
							config-aria={{'aria-label': t('Close dialog')}}
							tooltip-content={t('Close dialog')}
							on-click={() => {
								dispatch(() => {
									return {
										type: 'NOW_MODAL#OPENED_SET',
										payload: {value: false}
									};
								});
							}}
						/>
					</div>
					<div
						class={{
							'now-modal-body': true,
							'is-full-width': contentFullWidth
						}}>
						<slot>
							<p className="now-m-block--0">{content}</p>
						</slot>
					</div>
					{renderFooter(footerActionsObj, dispatch)}
				</div>
			</div>
		</div>
	);
};

/**
 * A modal is a container that displays over application content to give
 * critical information or to ask users to complete a task in the middle of a
 * workflow. When the modal opens, users cannot access the application.
 *
 * Note: Set `aria-hidden="true"` on all non-modal elements when now-modal is
 * open to provide the correct experience for screen reader users.
 *
 * ```jsx
 * <now-modal
 * 	opened={openedApp}
 * 	size='sm'
 * 	header-label='Hello!'
 * 	content='And this is the body of the modal.'
 * 	footer-actions='[
 * 		{
 * 			"variant": "primary",
 * 			"label": "Save"
 * 		}
 * 	]'
 * />
 * ```
 *
 * @seismicElement now-modal
 * @summary A popup component that temporarily blocks underlying content to provide information or ask the user for a decision.
 */
createEnhancedElement('now-modal', {
	properties: {
		/**
		 * Sets the size of the modal container. Most sizes automatically expand to
		 * fill the viewport when the screen size is small.
		 * @type {('sm'|'md'|'lg'|'fullscreen')}
		 */
		size: {
			default: 'sm',
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg', 'fullscreen']
			}
		},
		/**
		 * When true, the modal is open. Use `manage-opened` to override the
		 * default behavior and handle the `NOW_MODAL#OPENED_SET` action manually.
		 * @type {boolean}
		 */
		opened: {
			default: false,
			manageable: true,
			schema: {type: 'boolean'}
		},
		/**
		 * The text content for the modal header.
		 * @type {string}
		 */
		headerLabel: {schema: {type: 'string'}},
		/**
		 * The text content for the modal. If you want to display complex content,
		 * use the default slot instead. This property is ignored when an element
		 * is slotted in.
		 * @type {string}
		 */
		content: {schema: {type: 'string'}},
		/**
		 * When true, removes the horizontal padding around the body of the modal
		 * in order to fit wider content.
		 * @type {boolean}
		 */
		contentFullWidth: {default: false, schema: {type: 'boolean'}},
		/**
		 * An array of objects that defines the buttons in the modal footer.  You
		 * can define up to four buttons, set from right to left.
		 *
		 * The keys of each object are properties for `now-button`. See
		 * `now-button` for more documentation on these properties.
		 * @type {{ label: string, variant: string, disabled: boolean, clickActionType: string}[]}
		 */
		footerActions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		},
		/**
		 * WARNING: Do not use. This is a stop-gap while certain platform UI
		 * is pending conversion to Seismic during Rome.
		 * @private
		 */
		legacyMode: {
			default: false,
			schema: {type: 'boolean'}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the user clicks on a footer action item. Payload
		 * includes a reference to the clicked item and action. Set
		 * `action.clickActionType` to override the action type.
		 * @type {{action: Object}}
		 */
		'NOW_MODAL#FOOTER_ACTION_CLICKED': {},
		/**
		 * Dispatched when the user dismisses the modal by clicking the dismiss
		 * button or pressing `Escape` while the modal is open. Set the
		 * `manage-opened` property to override the default behavior and handle
		 * this action manually.
		 * @type {{value: boolean}}
		 */
		'NOW_MODAL#OPENED_SET': {}
	},
	slots: {
		/**
		 * Content added to this slot appears inside the body of the modal. When
		 * this slot is used, the `content` property is ignored.
		 * @defaultSlot
		 */
		defaultSlot: {}
	},
	initialState: {
		triggerEl: null,
		headerEl: null,
		headerLabelEl: null
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({
			host,
			properties,
			dispatch,
			action,
			updateState
		}) => {
			const {id} = action.meta;

			if (properties.opened) addModalToStack(id, dispatch);

			const headerLabelEl = host.nowRootNode.querySelector(
				'.now-modal-heading-label'
			);
			const headerEl = host.nowRootNode.querySelector(`#modalHeading-${id}`);
			updateState({headerLabelEl, headerEl});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({action, dispatch, host}) => {
			const {
				payload: {name, value, previousValue},
				meta: {id}
			} = action;

			if (name === 'opened') {
				if (value) {
					addModalToStack(id, dispatch);
				} else {
					layerEvents.remove(id);
				}
			}

			// If the number of footer actions is reduced, restore focus to the close button (DEF0115652)
			if (
				name === 'footerActions' &&
				((previousValue && !value) ||
					(value && previousValue && value.length < previousValue.length))
			) {
				host.nowRootNode.querySelector('#now-modal-close-button').focus();
			}
		},
		[COMPONENT_DISCONNECTED]: ({action, properties: {opened}}) => {
			if (opened) layerEvents.remove(action.meta.id);
		}
	},
	behaviors: [
		{
			behavior: focusBehavior,
			options: {trap: true}
		}
	],
	styles,
	view
});
